export enum EnglishLevel {
  BASIC = "Basic",
  CONVERSATIONAL = "Conversational",
  FLUENT = "Fluent",
  NATIVE = "Native",
}

export const englishLevelMap = Object.values(EnglishLevel).reduce(
  (accumulator, currentValue, currentIndex) => ({
    ...accumulator,
    [currentIndex]: currentValue,
  }),
  {}
);

export const getValueByEnglishLevel = (level: EnglishLevel) =>
  Object.values(EnglishLevel).findIndex((value) => value === level);
