import { FC, useState } from "react";
import { Badge, Col, Divider, FloatButton, Row, Typography } from "antd";
import FilterPanel from "./FilterPanel";
import { candidates } from "../api/data";
import Candidate from "./Candidate";
import { IFilterSettings } from "../interfaces/filterSettings";
import { getValueByEnglishLevel } from "../enums/englishLevel";

const Outstaff: FC = () => {
  const [filterSettings, setFilterSettings] = useState<IFilterSettings>({
    hourlyRate: { from: 20, to: 60 },
    workExpirience: { from: 0, to: 7 },
    english: { from: 0, to: 3 },
    ableToWorkIn: 25,
    requiredExpirience: [],
  });

  const filteredCandidates = candidates.filter(
    (c) =>
      c.rate >= filterSettings.hourlyRate.from &&
      c.rate <= filterSettings.hourlyRate.to &&
      c.workExpirience.value >= filterSettings.workExpirience.from &&
      c.workExpirience.value <= filterSettings.workExpirience.to &&
      getValueByEnglishLevel(c.english) >= filterSettings.english.from &&
      getValueByEnglishLevel(c.english) <= filterSettings.english.to &&
      c.ableToWorkIn.value <= filterSettings.ableToWorkIn * 7 &&
      filterSettings.requiredExpirience.every((item) => c.skills.includes(item))
  );

  return (
    <div style={{ margin: 30 }}>
      <Row gutter={[40, 40]}>
        <FilterPanel
          filterSettings={filterSettings}
          setFilterSettings={setFilterSettings}
        />

        <Col style={{ marginLeft: 350 }}>
          <Row align="middle" gutter={5}>
            <Col>
              <Typography.Title level={3} type="secondary">
                Available Xedrum Candidates
              </Typography.Title>
            </Col>
            <Col>
              <Badge
                showZero
                count={filteredCandidates.length}
                style={{ backgroundColor: "#52c41a" }}
              />
            </Col>
          </Row>

          <Row gutter={[20, 20]}>
            {filteredCandidates.map((c) => (
              <Candidate candidate={c} key={c.id} />
            ))}
          </Row>

          {filteredCandidates.length === 0 && (
            <Typography.Title
              level={2}
              style={{ marginTop: 100, color: "gray" }}
            >
              Cannot find suitable developer?
              <Divider />
              No worries, just bring a  and let's{" "}
              <Typography.Link
                href="https://calendly.com/dmelnyc_xedrum/intro-call-with-ceo-xedrum"
                target="_blank"
                style={{ fontSize: 30 }}
              >
                discuss
              </Typography.Link>{" "}
              your perfect candidate.
              <br />
              We will locate an expert in as little as{" "}
              <b style={{ color: "#0FBC4A" }}>two to three weeks</b>
            </Typography.Title>
          )}
        </Col>
      </Row>

      <FloatButton
        href="https://calendly.com/dmelnyc_xedrum/intro-call-with-ceo-xedrum"
        target="_blank"
        type="primary"
        shape="square"
        description="Book a call / Disscuss"
        style={{ width: 160, height: 40, textDecoration: "none" }}
      />
    </div>
  );
};

export default Outstaff;
