import { FC } from "react";
import { Card, Col, Divider, Row, Image, Tag, Typography } from "antd";
import { CheckCircleOutlined } from "@ant-design/icons";
import { ICandidate } from "../interfaces/candidate";

interface ICandidateProps {
  candidate: ICandidate;
}

const Candidate: FC<ICandidateProps> = ({ candidate }) => {
  return (
    <Col>
      <Card
        hoverable
        style={{ width: 300 }}
        cover={
          <Image
            src={`cv/${candidate.id}.png`}
            height={300}
            style={{ objectFit: "cover", objectPosition: "top" }}
          />
        }
      >
        <Row justify="space-between">
          <Row gutter={5}>
            <Col>
              <Typography.Text strong>{candidate.name}</Typography.Text>
            </Col>
            <Col>
              <CheckCircleOutlined style={{ color: "#87d068" }} />
            </Col>
          </Row>
          <Tag color="#87d068">{candidate.rate}$/h</Tag>
        </Row>
        <Row>
          <Typography.Text type="secondary">
            {candidate.position}
          </Typography.Text>
        </Row>
        <Typography.Text type="success">
          Able to work in {candidate.ableToWorkIn.text}
        </Typography.Text>

        <Divider />

        <Row justify="space-between">
          <Typography.Text type="secondary">Work expirience:</Typography.Text>
          <Typography.Text type="secondary">
            {candidate.workExpirience.text}
          </Typography.Text>
        </Row>
        <Row justify="space-between">
          <Typography.Text type="secondary">English:</Typography.Text>
          <Typography.Text type="secondary">
            {candidate.english}
          </Typography.Text>
        </Row>
        <Row justify="space-between">
          <Typography.Text type="secondary">Current location:</Typography.Text>
          <Typography.Text type="secondary">
            {candidate.location}
          </Typography.Text>
        </Row>

        <Divider />

        <Row gutter={[-5, 5]}>
          {candidate.skills.map((coloredSkill, index) => {
            const [skill, color] = coloredSkill.split(":");

            return (
              <Col key={`skill-${index}`}>
                <Tag color={color}>{skill}</Tag>
              </Col>
            );
          })}
        </Row>
      </Card>
    </Col>
  );
};

export default Candidate;
